#main_container {
  display: flex;
  background-image: url(C:/NFT/NFT/candy-machine-mint-main/src/banner.png);
  background-size: cover;
  height: 100vh;
  
}

#main_div {
  display: flex;
  padding: 30px;
  flex: 1;
  flex-direction: column;
  
}

#top_div {
  display: flex;
  justify-content: space-between;
}

#middle_div {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;

}

#center_div {
  display: flex;
  justify-content: center;
  background:  rgba(0,0,0,0.9);
  flex-direction: column;
  align-items: center;
  margin-right: 2%;
  padding: 5%;
}

#text_div {
  text-align: center;
}


